import React from "react"
import {Table, Col, Row, Container} from "react-bootstrap"

function average(data: number[]) { // 
	let sum = 0
	for (const n of data) sum += n
	return sum / data.length
}

function variance(data: number[]) { // 
	let sum = 0
	for (const n of data) sum += n * n
	return sum / data.length - (average(data) * average(data))
}

function standard_deviation(data: number[]) { // 
	return Math.sqrt(variance(data))
}

export function StatisticsTable(props: {array: number[]}) {
	return (
		<>
			<Container>
				<Row>
					<Col>
						<div style={{textAlign: "right"}}>
							<Table striped bordered>
								<tbody>
									<tr><td></td><td>{props.array.length}</td></tr>
									<tr><td></td><td>{average(props.array).toFixed(2)}</td></tr>
									<tr><td></td><td>{variance(props.array).toFixed(2)}</td></tr>
									<tr><td></td><td>{standard_deviation(props.array).toFixed(2)}</td></tr>
								</tbody>
							</Table>
						</div>
					</Col>
				</Row>
			</Container>
		</>
	)
}
